.spinner-container {
    @include inline-flexbox(center, center);
    width: 100%;
    height: 100vh;
    .spinner {
        border: 8px solid #f3f3f3;
        border-top: 8px solid #00a86b;
        border-radius: 50%;
        width: 60px;
        height: 60px;
        animation: spin 2s linear infinite;
    }

    @keyframes spin {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}
