.intro-container {
    @include inline-flexbox(center, center);
    width: 100%;
    height: 100vh;
    transition: opacity 1s ease-out;

    &.fade-out {
        opacity: 0;
    }

    .intro-circle {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background-color: #ccc;
    }
}
